.Banner {
  margin: 40px 0px;
  &-Logo {
    border-radius: 15px;
    width: 100%;
    height: 180px;
    object-fit: cover;
    filter: brightness(0.65);
  }
  &-Text {
    position: absolute;
    top: 180px;
    left: 40%;
    color: white;
    font-size: 35px;
    font-weight: normal;
  }
}
